export async function loader({ request }) {
    const { user, token } = await getCurrentUser(request);
    return json({ user, token });
}

function Kitchen() {
    const { user, token } = useLoaderData();

    const [searchParams, setSearchParams] = useSearchParams();
    const activeTab = searchParams.get('tab') || 'cafe';

    const setTab = (tab) => setSearchParams({ tab });

    const tabBase = "px-6 py-4 pl-0 font-medium transition-colors";
    const tabActive = "text-[var(--primary-color)] border-b-2 border-b-[var(--primary-color)]";
    const tabInactive = "text-gray-700";

    return (
        <AdminLayout user={user} token={token}>
            <div className="p-6">
                <div className="flex justify-between">
                    <h1 className="text-2xl font-semibold mb-4 text-[--primary-color]">Kitchen</h1>
                </div>

                <div className="flex gap-6 mb-4 border-b border-gray-200">

                    <NavLink
                        to="cafe"
                        className={({ isActive }) =>
                            `${tabBase} ${isActive ? tabActive : tabInactive}`
                        }
                    >
                        Cafe
                    </NavLink>

                    <NavLink
                        to="foodwaste"
                        className={({ isActive }) =>
                            `${tabBase} ${isActive ? tabActive : tabInactive}`
                        }
                    >
                        Food Waste
                    </NavLink>

                </div>

                {/* CHILD ROUTES RENDER HERE */}
                <Outlet />
            </div>
        </AdminLayout>
    )
}

export default Kitchen
